import { Injectable, ViewChild, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { Http, RequestOptions, Headers, Response } from '@angular/http';
import { Observable } from 'rxjs/Rx';

@Injectable()
export class AuthenticationService {
    private _baseUrl: string = '/api/v1/users'; 
    public token: string;
    public eLogin: EventEmitter<any> = new EventEmitter<any>();
    public allowed: any[] = [];

    get cleanToken() {
      return this.token.replace("JWT ", "");
    }
 
    constructor(
      private _http: Http,
      private _router: Router
    ) {
      //console.log('Auth init');
        // set token if saved in local storage
        var currentUser = JSON.parse(localStorage.getItem('currentUser'));
        this.token = currentUser && currentUser.token;
        if(this.checkAuth()) {
          this.init();
        }
    }
 
    login(username: string, password: string): Observable<boolean> {
      let bodyString = JSON.stringify({username: username, password: password});
      let headers = new Headers({'Content-type': 'application/json'});
      let options = new RequestOptions({headers: headers});

      return this._http.post(this._baseUrl + '/login', bodyString, options)
        .map((res) => {
          let token = res.json() && res.json().token;
          if (token) {
              this.token = token;
              localStorage.setItem('currentUser', JSON.stringify({ username: username, token: token }));
              this.getAllowed();
              
              this.eLogin.emit();
              this._router.navigate(["/"]);
              return true;
          } else {
              return false;
          }})
			  .catch((error: any) => Observable.throw(error || 'Server error'));
  
    }

    checkAuth(): boolean {
      return !!this.token;
    }

    checkRule(rule): boolean {
      return (this.allowed.indexOf(rule) >= 0);
    }
 
    logout(): void {
        // clear token remove user from local storage to log user out
        this.token = null;
        localStorage.removeItem('currentUser');
        this._router.navigate(['/login']);
    }

    private init() {
      this.getAllowed();
    }

    getAllowed() {
      let headers = new Headers({'Content-type': 'application/json', 'Authorization': this.token});
      let options = new RequestOptions({headers: headers});

      let allowed$ = this._http.get(this._baseUrl + '/allowed', options)
        .map((res: Response) => res.json())
        .catch((error) => {
          this.logout();
          return Observable.throw(error || 'Server error');
        });

      allowed$.subscribe((allowed) => this.allowed = allowed);
    }

}



