import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";

import { UserLoginComponent } from './user-login/user-login.component';
import { UserFormComponent } from './user-form/user-form.component';

import { AuthenticationService } from '../_services/authentication.service';
import { HttpClient } from '../_services/http-client.service';

@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule,
    MaterializeModule
  ],
  declarations: [UserLoginComponent, UserFormComponent],
  providers: [HttpClient], //AuthenticationService, 
  exports: [UserLoginComponent, UserFormComponent]
})
export class UsersWidgetModule { }


