import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { IUser } from '../../user';

@Component({
  selector: 'app-user-form',
  templateUrl: './user-form.component.html',
  styleUrls: ['./user-form.component.css']
})
export class UserFormComponent implements OnInit {

  @Input('form') form: FormGroup = this.createFormGroup();

  @Output('submitForm')
    eSubmit: EventEmitter<IUser> = new EventEmitter<IUser>();
  @Output('close')
    eClose: EventEmitter<string> = new EventEmitter<string>();

  userGroups: any[] = [
    { name: "admin", title: "Администратор" },
    { name: "user-admin", title: "Администратор (ресепшен)" },
    { name: "user-spec", title: "Специалист" },
    { name: "user-buh", title: "Бухгалтер" },
    { name: "boss", title: "Like'a boss!" },
    { name: "user", title: "Пользователь" },
    { name: "develop", title: "Разработчик" }
  ];

  constructor(
    private _fb: FormBuilder
  ) { }

  ngOnInit() {
  }

  onSubmit() {
    if(this.form.valid) {
      this.eSubmit.emit(this.form.value);
    }
  }

  onClose() {
    this.eClose.emit();
  }

  private createFormGroup() {
    return this._fb.group({
      username: this._fb.control('', [<any>Validators.required, <any>Validators.minLength(2)]),
      passwords: this._fb.group({
        password: this._fb.control('', [<any>Validators.required]),
        passwordAgain: this._fb.control('', [<any>Validators.required]),
      }, this.matchingPasswords('password', 'passwordAgain')),
      group: this._fb.control(''),
			active: this._fb.control('')
    });
  }

  private matchingPasswords(passwordKey: string, passwordConfirmationKey: string): any {
    return (group: FormGroup) => {
      let passwordInput = group.controls[passwordKey];
      let passwordConfirmationInput = group.controls[passwordConfirmationKey];
      if (passwordInput.value !== passwordConfirmationInput.value) {
        return passwordConfirmationInput.setErrors({notEquivalent: true})
      }
    }
  }

}



