import { Component, OnInit } from '@angular/core';
import { ToolbarService } from '../toolbar.service';
import { AuthenticationService } from '../../+users/_services/authentication.service';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-toolbar',
  templateUrl: './toolbar.component.html',
  styleUrls: ['./toolbar.component.css']
})
export class ToolbarComponent implements OnInit {
  public mode: string = 'default';
  public searchText: string;
  public searchAction: Function;
  public tools$: Observable<any>;

  public navOptions: any[] = [
    {
      edge: 'left',
      closeOnClick: true,
      draggable: true
    }
  ];

  constructor(
    public _toolbar: ToolbarService,
    private _auth: AuthenticationService
  ) { }

  ngOnInit() {
    this.reloadTools();
    this._auth.eLogin.subscribe(() => this.reloadTools());
    this.tools$.subscribe();
  }

  reloadTools() {
    this.tools$ = this._toolbar.getTools();
  }

  addAction(action, changeMode?) : void {
    if(changeMode == 'search') {
      this.searchAction = action;
    } else {
      action();
    }
    if(changeMode) this._toolbar.setMode(changeMode);
  }

  toggleMode(name) : void {
    let mode = this._toolbar.mode == name ? 'default' : name;
    this._toolbar.setMode(mode);
  }

  search(): void {
    this._toolbar.getAction('search')(this.searchText);
  }

  keyDownFunction(event): void {
    if(event.keyCode == 13) {
      this.search();
    }
  }

  onExit(): void {
    this._auth.logout();
    this.reloadTools();
  }
}



